import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import MegaNavSectionDesktop from './MegaNavSectionDesktop';
import useOnClickOutside from '../../helpers/hooks/useOnClickOutside';
import { useAppContext } from '../AppContext';

const Wrapper = styled.div`
  position: absolute;
  z-index: 2;
  top: ${prop => (prop.promoBannerExists ? '92px' : '72px')};
  width: 100%;
  left: 0;
  padding: 0px;
  display: block;
`;

const GridContainer = styled.div`
  box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.3);
  background: #fff;
  display: flex;
`;

const MegaNavDesktop = ({ sections = [] }) => {
  const ref = React.useRef(null);

  const promoBannerExists = true, appContextDispatcher = null;

  return (
    <Wrapper promoBannerExists={promoBannerExists}>
      <GridContainer ref={ref}>
        {sections.length &&
          sections.map(({ items, label, icon }, index) => {
            return (
              <MegaNavSectionDesktop
                key={index}
                icon={icon}
                items={items}
                label={label}
              />
            );
          })}
      </GridContainer>
    </Wrapper>
  );
};

MegaNavDesktop.propTypes = {
  sections: PropTypes.arrayOf(PropTypes.object)
};

export default MegaNavDesktop;
